body  {
  user-select: none;
}

.wrapper {
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}

#container {
    position: fixed;
    width: 100%;
    height: 100vh;
    overflow-y: hidden;
  }
  
  #textCont {
    position: absolute;
    align-items: center;
    display: flex;
    justify-content: center;
    width: 100%;
    top: 0;
    height: 100vh;
  }
  
  #text {
    font-family: 'Noto Sans TC';
    color: #3632329f;
    user-select: none;
    overflow-y: hidden;
    font-weight: 900;
    font-size: 7vw;
    position: absolute;
    margin-left: 27%;
    margin-top: 29vh;
    transition: 0.4s;
  }
  
  @media only screen and (max-width: 700px) {
    #container {
      height: 85vh;
    }
    #text {
      color: #363232c9;
      left: -40px;
      font-size: 50px;
      bottom: 40px;
    }

  }
  
  .center {
    position: absolute;
    left: 50%;
    top: 95%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);  
}

  #message {
  
      user-select: none;
      font-family: 'Noto Sans TC';
      overflow-y: hidden;
      width: 100%;
      color: #363232af;
      text-align: center;
      font-weight: 900;
      font-size: 18px;
  
  }